@use 'sass:color';
// Font Variable
//$pf: 'Open Sans', sans-serif;
//$pf:'Poppins', sans-serif;
$pf: 'Quicksand', sans-serif;
$icon-font: 'Font Awesome 6 Pro';

// Transition Variable
$transition: 0.4s cubic-bezier(0.25, 1, 0.5, 1);

// Color Variable
$theme-color: #fecc18;
$theme-color-darker: #FFBF00;
$theme-color-text: #916c00;
$slider-color:#5e5640;
$slider-border-color: #fecc18;
$background-light-1: #fff;
$background-light-2: #FEFBF0;
$background-light-3: #f8f9fa;
$background-dark-1: #3B3B3B;
$background-dark-2:#373737;
$background-dark-3: #F7F7F6;
$background-dark-4: #1d1d1d;
$body-font-color:#333333;
$heading-font-color: #000000;
$body-bg:#fff;
$footer-bg: #3D3E3E;
$copyright-bg: #383939;
$smoke-color: rgba(0, 102, 252, 0.2);
$title-color: #201D1D;
$light-color: #9B9CA0;
$error-color: #dc3545;
$success-color: #73DC96;
$blue-color: #0a58ca;
$text-dark-color: $background-dark-1;
$form-control-border: #e0e0e0;

