// Button Color Variables
$btn-theme-color: #fecc18;
$btn-theme-color-hover: color.adjust($btn-theme-color, $lightness: -10%);
$btn-theme-color-active: color.adjust($btn-theme-color, $lightness: -15%);

///////////////
// Solid Color
///////////////
$white: #fff;
$offwhite: #FFF9E7;
$white-1:  #f1f1f1;
$black: #000;


////////////////////////
// Light Version Color
////////////////////////
