$bg:#f3f3f3;


/////////////
// Social
/////////////
$bg-social: #f7f7f7;
$facebook: #3B5998;
$twitter: #1DA1F2;
$youtube: #cd201f;
$google-plus: #db4439;
$linkedin: #007bb6;
$instagram: #c32aa3;
$pinterest: #bd081c;
$google: #4285f4;
$snapchat: #fffc00;
