////////////////
//Font Size
///////////////
$bfs-normal: 16px;
$bfs-regular: 14px;
$bfs-light: 12px;

// Device Variation
$hd: 1921px; // Large Device Than 1920
$xl: 1500px; // Extra large Device
$ml: 1399px; // Medium Large Device
$lg: 1199px; // Large Device (Laptop)
$md: 991px; // Medium Device (Tablet)
$sm: 767px; // Small Device
$xs: 575px; // Extra Small Device
$vxs: 375px; // Very Extra Small Device

// Dropdown Variables
$dropdown-width: 60px;
$dropdown-bg: $background-dark-1;
$dropdown-border-radius: 8px;
$dropdown-box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
$dropdown-item-padding: 10px 15px;
$dropdown-item-hover-bg: rgba($theme-color, 0.1);
$dropdown-item-active-bg: rgba($theme-color, 0.2);
$dropdown-item-color: $offwhite;
$dropdown-item-hover-color: $theme-color;
$dropdown-transition: all 0.3s ease-in-out;

// Dropdown Font Variables
$dropdown-font-family: $pf;  // Use the primary font
$dropdown-font-size: 14px;   // Base font size
$dropdown-font-weight: 500;  // Medium weight
$dropdown-line-height: 1.5;  // Consistent line height
$dropdown-letter-spacing: 0.5px;  // Slight letter spacing for readability